.App {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  background-color: #2f2932;
}
main {
  min-height: 240px;
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.calculator {
  min-width: 320px;
  margin: auto;
  padding: 24px;
  box-sizing: border-box;
  background-color: #000000;
  border-radius: 8px;
}
.display {
  max-width: 100%;
  min-height: 100px;
  height: 18vh;
  padding: 0 4px 16px;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  color: white;
  font-size: 7vmin;
}
.keyboard {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-gap: 12px;
}
.keyboard_section {
  display: grid;
  grid-gap: 12px;
  justify-items: center;
  align-items: center;
}
.keyboard__service {
  grid-column: 1 / 4;
  grid-row: 1;
  grid-template-columns: 1fr 1fr 1fr;
}
.keyboard__operators {
  grid-column: 4;
  grid-row: 1 / 6;
  display: grid;
  grid-gap: 12px;
  justify-items: center;
  align-items: center;
}
.keyboard__digits {
  grid-column: 1 / 4;
  grid-row: 2 / 6;
  grid-template-columns: repeat(3, 1fr);
}
.keyboard__key {
  width: 10vmin;
  min-width: 59px;
  height: 10vmin;
  min-height: 59px;
  position: relative;
  margin: 0;
  padding: 0;
  background-color: #333333;
  border: none;
  border-radius: 50%;
  font-size: 4vmin;
  color: white;
  user-select: none;
}
.keyboard__key:focus {
  outline: none;
  border: none;
}
.keyboard__key_double {
  grid-column: span 2;
  justify-self: flex-start;
  background-color: unset;
  border-radius: unset;
}
.keyboard__key_double::before {
  width: 50%;
  height: 100%;
  display: block;
  background-color: #333333;
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  transform: translateX(calc(100% - 6px));
}
.keyboard__key_double::after {
  width: 100%;
  height: 100%;
  display: block;
  background-color: #333333;
  border-radius: 0 50% 50% 0;
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  transform: translateX(calc(100% + 12px));
}
.keyboard__key_service {
  background-color: #a6a5a6;
}
.keyboard__key_operator {
  background-color: #bf6e00;
}
.keyboard__highlight {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  background-color: #ffffff;
  border-radius: 50%;
  opacity: 0;
}
.keyboard__doubleHighlight {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  border-radius: 50% 0 0 50%;
  background-color: #333333;
}
.keyboard__key:focus .keyboard__highlight, .keyboard__key:hover .keyboard__highlight {
  opacity: .2;
}
.keyboard__key:active .keyboard__highlight {
  opacity: .5;
}
.keyboard__key:focus::before, .keyboard__key:focus::after, .keyboard__key:focus .keyboard__doubleHighlight,
.keyboard__key:hover::before, .keyboard__key:hover::after, .keyboard__key:hover .keyboard__doubleHighlight
 {
  background-color: #5c5c5c;
}
.keyboard__key:active::before, .keyboard__key:active::after,
.keyboard__key:active .keyboard__doubleHighlight {
  background-color: #999999;
}
.keyboard__value {
  position: relative;
}
footer {
  width:100%;
  height:60px;
  padding:0 10px;
  display:flex;
  justify-content:flex-end;
  align-items:center;
  background-color:#bf5555;
  box-sizing:border-box;
}
.footer__link {
  text-decoration:none;
  color:#000;
  font-weight:600;
}
@media (max-width: 414px), (max-width: 812px) and (max-height: 414px) {
  .display {
    font-size: 13vmin;
  }
  .keyboard__key {
    width: 18vmin;
    height: 18vmin;
    font-size: 8vmin;
  }
}